* {
  box-sizing: border-box;
}

.search-form {
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 25px;
  display: flex;
}

.gallery {
  max-width: 1140px;
  grid-gap: 24px;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  margin-bottom: 25px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.photo-card {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: space-evenly;
  display: flex;
}

.card-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.info {
  grid-gap: 5px;
  text-align: center;
  letter-spacing: .03em;
  grid-template-columns: repeat(4, 1fr);
  padding: 5px;
  font-size: 10px;
  line-height: 1.1875;
  display: grid;
}

.load-more {
  width: 120px;
  height: 30px;
  color: #fff;
  background-color: #2c4de2;
  border: none;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.is-hidden {
  display: none;
}

